@font-face {
  font-family: Film P3;
  src: url("FilmP3Bold.57ba16a8.woff"), url("FilmP3Bold.d206194d.ttf");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Montserrat;
  src: url("Montserrat-Light.0099fdcd.woff"), url("Montserrat-Light.a03ff5f8.ttf");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Montserrat;
  src: url("Montserrat-Regular.8e2795a5.woff"), url("Montserrat-Regular.a32550f7.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Montserrat;
  src: url("Montserrat-Medium.b7fc8e4f.woff"), url("Montserrat-Medium.dc3ce8d6.ttf");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Montserrat;
  src: url("Montserrat-Bold.11f3b504.woff"), url("Montserrat-Bold.96db3e27.ttf");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Times New Roman;
  src: url("TimesNewRomanPS-BoldMT.67f8fb45.woff"), url("TimesNewRomanPS-BoldMT.dbf1aeb7.ttf");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Times New Roman;
  src: url("TimesNewRomanPSMT.1fef991d.woff"), url("TimesNewRomanPSMT.0364e0b1.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

body {
  letter-spacing: .02em;
  font-family: Montserrat, sans-serif;
  font-size: 12px;
}

.list {
  list-style: none;
}

.link {
  font-family: inherit;
  text-decoration: none;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  font-family: inherit;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 414px) {
  .container {
    width: 414px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

:root {
  --logo-theme-dark: #fff;
  --logo-theme-light: #494848;
  --subtitle-color: #b4b4b4;
  --accent-color: #f3c653;
  --primary-text-color: #5f5f5f;
  --secondary-text-color: #7a7a7a;
  --accent-text-color: #c79002;
  --logo-font: "Film P3";
  --main-font: "Montserrat";
  --additional-font: "Times New Roman";
  --background-lighter: #fffdee;
  --background-darker: #f0ecd6;
  --footer-background: #0e0c07;
  --transition-fast: 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-slow: 500ms cubic-bezier(.4, 0, .2, 1);
}

header {
  z-index: 3;
  width: 100%;
  padding-top: 16px;
  position: absolute;
  top: 0;
  right: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  letter-spacing: .3em;
  color: var(--logo-theme-dark);
  transition: var(--transition-slow);
  font-family: Film P3;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.08333;
  text-decoration: none;
  display: block;
}

.header__logo:hover, .header__logo:focus {
  transform: scale(1.2);
}

.header__nav-list {
  align-items: center;
  gap: 20px;
  display: flex;
}

.header__nav-link {
  letter-spacing: .02em;
  color: var(--accent-color);
  transition: var(--transition-slow);
  align-self: center;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
}

.header__tel-icon {
  fill: var(--accent-color);
  transition: var(--transition-slow);
}

@media screen and (max-width: 360px) {
  .header__logo {
    font-size: 32px;
  }

  .header__nav-link {
    font-size: 14px;
  }

  .header__nav-list {
    gap: 10px;
  }
}

.header__nav-items {
  gap: 6px;
  display: flex;
}

.header__nav-tel {
  color: var(--logo-theme-dark);
  transition: var(--transition-slow);
  line-height: 0;
  display: flex;
}

.header__tel-number {
  transition: var(--transition-slow);
  align-self: center;
  display: none;
}

@media screen and (min-width: 1024px) {
  .header__tel-number {
    display: block;
  }

  .header__nav-tel {
    letter-spacing: .02em;
    border: 1px solid rgba(243, 198, 83, .7);
    border-radius: 2px;
    padding: 8px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 400;
    line-height: .823529;
  }

  .header__nav-items {
    gap: 32px;
  }

  .header__nav-list {
    gap: 72px;
  }

  .header__nav-link {
    font-size: 20px;
    line-height: 1.2;
  }
}

.header__social-list {
  display: none;
}

@media screen and (min-width: 1280px) {
  .header__social-list {
    gap: 20px;
    display: flex;
  }
}

.header__social-link {
  fill: var(--accent-color);
  transition: var(--transition-slow);
}

.header__social-link:hover, .header__social-link:focus {
  fill: var(--accent-text-color);
}

.header__nav-link:hover, .header__nav-link:focus {
  color: var(--accent-text-color);
}

.header__nav-tel:hover .header__tel-icon, .header__nav-tel:focus .header__tel-icon {
  fill: var(--accent-text-color);
}

.header__nav-tel:hover .header__tel-number, .header__nav-tel:focus .header__tel-number {
  color: var(--accent-text-color);
}

.swiper {
  height: auto;
  width: 100%;
}

.slider_container {
  padding: 0;
}

.swiper__container {
  position: relative;
}

.swiper-slide {
  width: 100%;
  height: auto;
}

.slider__arrows {
  justify-content: space-between;
  display: flex;
}

.s-button-prev {
  z-index: 2;
  width: 40px;
  height: 40px;
  fill: var(--accent-color);
  cursor: pointer;
  transition: var(--transition-slow);
  background-color: rgba(0, 0, 0, .4);
  border: none;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 1%;
}

.s-button-next {
  z-index: 2;
  width: 40px;
  height: 40px;
  fill: var(--accent-color);
  cursor: pointer;
  transition: var(--transition-slow);
  background-color: rgba(0, 0, 0, .4);
  border: none;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  right: 1%;
}

.swiper__gradient {
  height: 115px;
  width: 100%;
  z-index: 2;
  background-image: linear-gradient(#000, rgba(0, 0, 0, .55), rgba(0, 0, 0, 0));
  position: absolute;
  top: 0;
}

.room-title {
  letter-spacing: .05em;
  color: var(--logo-theme-dark);
  font-family: Times New Roman;
  font-size: 28px;
  font-style: normal;
  font-weight: 700;
  line-height: 32px;
}

.room-text {
  max-width: 325px;
  letter-spacing: .02em;
  color: var(--logo-theme-dark);
  margin-top: 14px;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 15px;
}

.room-social-network-list {
  gap: 8px;
  margin-right: 70px;
}

@media screen and (max-width: 360px) {
  .room-social-network-list {
    margin-right: auto;
  }

  .room-title {
    letter-spacing: .05em;
    font-size: 24px;
    line-height: 1.08333;
  }

  .room-text, .socials_and_prices {
    margin-top: 5px;
  }

  .info__wrapper {
    bottom: 5px;
  }
}

button.room-prices-button {
  box-sizing: border-box;
  width: 232px;
  height: 48px;
  letter-spacing: .02em;
  color: var(--accent-color);
  background: rgba(0, 0, 0, .4);
  border: 2px solid #fff;
  border-radius: 25px;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  position: relative;
  left: 5px;
}

.room-location {
  box-sizing: border-box;
  width: 210px;
  height: 47px;
  border: 1px solid rgba(243, 198, 83, .7);
  border-radius: 2px;
  align-items: center;
  padding: 8px 3px 9px 6px;
  display: flex;
}

.address__map {
  letter-spacing: .01em;
  color: #e5e5e5;
  font-style: normal;
  font-weight: 400;
  line-height: 15px;
}

.info__wrapper {
  z-index: 2;
  position: absolute;
  bottom: 16px;
  left: 0;
  right: 0;
}

.socials_and_prices {
  align-items: center;
  margin-top: 16px;
  display: flex;
}

@media screen and (max-width: 360px) {
  .socials_and_prices {
    margin-top: 10px;
  }

  button.room-prices-button {
    transform: translateX(-5px);
  }
}

.hero__slider__adress {
  z-index: 2;
  position: absolute;
  top: 64px;
  right: 15px;
}

@media screen and (min-width: 1024px) {
  .room-title {
    letter-spacing: .05em;
    font-size: 36px;
    line-height: 1.15;
  }

  .room-text {
    font-size: 1 6px;
    letter-spacing: .02em;
    line-height: 1.21875;
  }

  .room-social-network-list {
    align-items: center;
    gap: 21px;
    margin-right: 261px;
    display: flex;
  }

  .socials_and_prices {
    height: 104px;
    margin-left: 325px;
  }

  .button-container {
    align-self: baseline;
  }

  .hero__slider__adress {
    top: 630px;
    right: 767px;
  }
}

@media screen and (min-width: 1280px) {
  .room-social-network-list, .hero__slider__adress {
    display: none;
  }

  .socials_and_prices {
    height: auto;
  }

  .button-container {
    align-self: center;
    transform: translateY(30%);
  }

  .info__wrapper {
    justify-content: space-between;
    display: flex;
  }

  .appartment__info {
    margin-bottom: 32px;
  }
}

.social-network-link {
  fill: var(--accent-color);
  transition: var(--transition-slow);
}

.social-network-link:hover, .social-network-link:focus {
  fill: var(--accent-text-color);
}

.room-prices-button {
  transition: background-color var(--transition-fast), border var(--transition-fast);
}

.room-prices-button:hover, .room-prices-button:focus {
  background-color: var(--logo-theme-dark);
  border: 2px solid var(--accent-color);
}

.hero__slider__adress {
  transition: transform var(--transition-slow);
}

.hero__slider__adress:hover, .hero__slider__adress:focus {
  transform: scale(1.05);
}

.section-room-convenience {
  background-color: var(--background-lighter);
  padding-top: 32px;
  padding-bottom: 32px;
}

@media screen and (min-width: 1024px) {
