    display: flex;
  }
}

.room-booking-button {
  box-sizing: border-box;
  max-width: 384px;
  min-height: 64px;
  border: 1px solid var(--accent-color);
  color: var(--logo-theme-dark);
  transition: background-color var(--transition-slow), border var(--transition-slow);
  background: #000;
  border-radius: 25px;
  margin: 32px auto 20px;
  padding: 20px 42px;
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
