}

.room-booking-button:hover, .room-booking-button:focus {
  background: var(--accent-color);
  color: #000;
  font-weight: 500;
}

@media screen and (min-width: 1024px) {
  .room-booking-button {
    max-width: 435px;
    height: 64px;
    margin-top: 40px;
    margin-right: 0;
    padding: 18px 38px;
    font-size: 24px;
    line-height: 29px;
  }
}

@media screen and (min-width: 1280px) {
  .room-booking-button {
    margin-right: 151px;
  }
}

.room-back {
  letter-spacing: .02em;
  color: var(--accent-text-color);
  transition: var(--transition-slow);
  justify-content: center;
  margin-top: 20px;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  display: flex;
}

.room-back:hover, .room-back:focus {
  color: var(--accent-color);
  fill: var(--accent-color);
}

@media screen and (min-width: 1024px) {
  .room-back {
    justify-content: end;
    margin-top: 32px;
    margin-right: 0;
    font-size: 20px;
    line-height: 24px;
  }
}

@media screen and (min-width: 1280px) {
  .room-back {
    margin-right: 151px;
  }
}

.room-back-icon {
  fill: currentColor;
  margin-left: 12px;
}

@media screen and (min-width: 1024px) {
  .room-back-icon {
    margin-left: 16px;
  }
}

.footer {
