  align-self: center;
  margin-left: 44px;
  font-size: 40px;
  font-weight: 700;
  line-height: 1.075;
}

@media screen and (max-width: 360px) {
  .logo-link-footer {
    margin-left: 0;
  }
}

.logo-link-footer:hover, .logo-link-footer:hover {
  color: var(--accent-text-color);
}

@media screen and (min-width: 1024px) {
  .logo-link-footer {
    margin-left: 107px;
