  }
}

.pages-link {
  letter-spacing: .02em;
  color: var(--accent-color);
  transition: var(--transition-slow);
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

.pages-link:hover, .pages-link:focus {
