}

.pages-item:not(:last-child) {
  margin-bottom: 16px;
}

.footer-wrapper-right-side {
  margin-left: auto;
  margin-right: 15px;
}

@media screen and (min-width: 1024px) {
  .footer-wrapper-right-side {
    margin-right: 75px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .footer-wrapper-right-side {
    margin-right: 101px;
  }
}

.social-list-footer {
  align-items: center;
  gap: 22px;
  display: flex;
}

@media screen and (min-width: 1024px) {
  .social-list-footer {
    flex-direction: column-reverse;
    align-items: stretch;
    gap: 12px;
  }
}

.social-item-footer {
  line-height: 0;
}

@media screen and (min-width: 1024px) {
  .social-item-footer {
    min-width: 144px;
  }
}

.social-icon {
  fill: var(--accent-color);
  transition: var(--transition-slow);
}

.social-link:hover .social-icon, .social-link:focus .social-icon {
  fill: var(--accent-text-color);
}

@media screen and (min-width: 1024px) {
  .social-link {
    border: .5px solid var(--accent-color);
    transition: var(--transition-slow);
    border-radius: 2px;
    justify-content: space-between;
    align-items: center;
    padding: 8px 12px;
    display: flex;
  }

  .social-link:hover, .social-link:focus {
