}

@media screen and (min-width: 1024px) {
  .social-link span {
    font-family: var(--main-font);
    letter-spacing: .02em;
    color: var(--accent-color);
    transition: var(--transition-slow);
    font-size: 16px;
    font-weight: 400;
    line-height: 1.25;
    display: inline;
  }
}

.social-link:hover span, .social-link:focus span {
  color: var(--accent-text-color);
}

.overlay {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  width: 100%;
  height: 100%;
  z-index: 101;
  transition: opacity var(--transition-slow), visibility var(--transition-slow);
  background-color: rgba(0, 0, 0, .65);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 414px;
  max-height: 98%;
  z-index: 102;
  background-color: var(--background-lighter);
  transition: opacity var(--transition-slow), visibility var(--transition-slow), transform var(--transition-slow);
  border-radius: 2px;
  padding: 60px 15px;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%)scale(.2);
}

@media screen and (min-width: 1024px) {
  .modal {
    max-width: 712px;
    padding: 60px;
  }
}

.overlay.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.modal.active {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%)scale(.2);
}

.modal__cross {
  width: 18px;
  height: 18px;
  stroke: var(--footer-background);
  stroke-width: 2px;
  cursor: pointer;
  transition: stroke var(--transition-fast), stroke-width var(--transition-fast);
  position: absolute;
  top: 16px;
  right: 16px;
}

.modal__cross:hover, .modal__cross:focus {
  stroke: var(--accent-text-color);
  stroke-width: 5px;
}

.modal__title {
  text-align: center;
  letter-spacing: .05em;
  margin-bottom: 40px;
  font-family: Times New Roman;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.16667;
}

.modal-group {
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
}

.modal_text {
  letter-spacing: .05em;
  color: gray;
  margin-bottom: 20px;
  font-family: Times New Roman;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 28px;
}

.modal-input {
  border: 1px solid var(--accent-text-color);
  letter-spacing: .05em;
  color: gray;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 2px;
  margin-bottom: 8px;
  padding: 17px 12px;
  font-family: Times New Roman;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.15;
}

.modal-label {
  letter-spacing: .02em;
  color: var(--accent-text-color);
  margin-left: 8px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
}

.modal-select {
  width: 100%;
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
